import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
//import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import {Epic, Tabbar, TabbarItem, Panel, PanelHeader, Button, Div, Group} from '@vkontakte/vkui/dist';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28More from '@vkontakte/icons/dist/28/more';

import Home from './panels/Home';
import Meropriyatia from './panels/meropriyatia';
import Raspisanie from './panels/raspisanie';
import PD from './panels/pd/PD';
import PSA from './panels/psa/PSA';
import Y from './panels/y/Y';
import PD197 from './panels/pd/PD-197';
import PD198 from './panels/pd/PD-198';
import PD199 from './panels/pd/PD-199';
import PD298 from './panels/pd/PD-298';
import PD298a from './panels/pd/PD-298a';
import PD299 from './panels/pd/PD-299';
import Raspd197 from './panels/pd/raspisanie/raspd197';


class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeStory: 'Home',
      fetchedUser: null
    }
    this.onStoryChange = this.onStoryChange.bind(this);
  }

  componentDidMount () {
  	bridge.send("VKWebAppGetUserInfo", {}).then(e => this.setState({fetchedUser: e.data}))
  }

  onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story })
  }



  render () {

    return (
      <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'Home'}
            data-story="Home"
          ><Icon28Newsfeed /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'Home'}
            data-story="Home"
          ><Icon28Newsfeed /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'profile'}
            data-story="profile"
          ><Icon28More /></TabbarItem>
        </Tabbar>
      }>
		<View id="Home" activePanel="Home">
 		 <Home id="Home" fetchedUser={this.state.fetchedUser} />
		</View>
        <View id="profile" activePanel="messages">
          <Panel id="messages">
            <PanelHeader>Сообщения</PanelHeader>
          </Panel>
        </View>
      </Epic>
    )
  }
}

export default App;